import React, { Component } from 'react'
import { Container} from 'react-bootstrap'
import "./Login.css";
import axios from "axios"




export default class Login extends Component {
    constructor(){
        super();
        this.state ={
            fields: {},
            errors: {},
        }
    }
    
    onChange =(e)=>{
    
        console.log("e.target.name"+e.target.name)
        this.setState({
            [e.target.name] : e.target.value
        })
        
    }
onSubmit =()=>{
let data = ({
    Email: this.state.Email,
    Password: this.state.Password
})

    axios.post("http://localhost:3000/login",data).then(response => {
        alert("response:"+response);
    })
    .catch(err=>{
console.log("err"+err);
    });
}

    
    render() {
        return (
        <div className="Login">

        
        <Container className= "C">
            <div>
            
            <form className="f" onSubmit={this.onSubmit}>
                <h3>Sign In</h3>

                <div className="form-group">
                    <label>Email address</label>
                    <input type="email" className="form-control" placeholder="Enter email" name="Email" onChange={this.onChange} value={this.state.fields["Email"]}/>
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password" className="form-control" placeholder="Enter password" name="Password" onChange={this.onChange} value={this.state.fields["Password"]} />
                </div>
                
                <div className="buttonContainer"><button type="submit" className="btn btn-primary btn-block" onClick={Login}>Submit</button></div>
                <div className="forgot-password">
                    <p>
                    Forgot <a href="/">password?</a>
                </p>
                </div>
                 </form>
        </div>
        </Container>
        </div>
    );
    }
}



